html,body{
  height: 100%;
}

body{
  margin: 0;
  padding: auto;
  font-family: helvetica;
  background: linear-gradient(180deg, rgba(9,35,175,1)0%, rgba(0,0,0,1)100%);
  color: #FFF;
}

button{
  background: #1646a0;
  color: #FFF;
  padding: 0px 45px;
  border: 2px solid #FFF;
  border-radius: 25px;
  height: 50px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.2em;
  cursor: pointer;
  transition: .4s;
}

button:hover{
  background-color: #0923af;
}
